<template>
  <div class="changePasswd">
    <mt-header title="">
      <router-link to="/more" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
      <mt-button @click.native="handleSave" slot="right">保存</mt-button>
    </mt-header>
    <div class="changePasswdBody">
      <div class="changePasswdBodyItem">
        <mt-field label="" placeholder="输入当前密码" v-model="currentPasswd"></mt-field>
      </div>
      <div class="changePasswdBodyItem">
        <mt-field label="" v-model="newPasswd" placeholder="输入新密码"></mt-field>
      </div>
      <div class="changePasswdBodyItem">
        <mt-field v-model="newPasswdRepeat" label="" placeholder="再次输入新密码"></mt-field>
      </div>
    </div>  
  </div>
</template>

<script>
import { mapGetters, mapActions} from 'vuex'
import { Toast } from 'mint-ui';
export default {
  name: 'changePasswd',
  data () {
    return {
      currentPasswd: '',
      newPasswd: '',
      newPasswdRepeat: ''
    }
  },
  computed: {
    ...mapGetters({
      phone: 'getPhone'
    })
  },
  methods: {
    ...mapActions({
      changePasswd: 'changePasswd'
    }),
    handleSave: function() {
      if(!(this.currentPasswd && this.newPasswd && this.newPasswdRepeat)) {
        Toast({
          message: '请设置正确的密码',
          position: 'bottom',
          duration: 1000
        });
      } else {
        if(this.newPasswd != this.newPasswdRepeat) {
          Toast({
            message: '两次输入的新密码不同',
            position: 'bottom',
            duration: 1000
          });
        } else {
          let data = {};
          data.username = this.phone;
          data.password = this.currentPasswd;
          data.newPassword = this.newPasswd;
          this.changePasswd(data).then(response => {
            Toast({
                message: '修改成功',
                position: 'bottom',
                duration: 1000
              });
            this.$router.push('/index');
          },error => {
            if(error.body.code == 2002) {
              Toast({
                message: '当前密码错误,修改失败',
                position: 'bottom',
                duration: 2000
              });
            }
          })
        }
      }
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.passwdPt {
  width: 10%;
  height: 1%;
}
.changePasswdBodyItem{
  display:flex;
  justify-content: space-between;
  border-bottom:1px solid #d06091;
}
/**/
.changePasswdBody {
  margin-top: 5%;
  padding: 2%;
}

</style>



